import helper from '../../common/common.js';
export default {
	data() {
		return {
			autograph:'',
			user_id:''
		}
	},
	onShow(){
		uni.getStorage({
			key:'userinfo',
			success:(res)=>{
				console.log(res)
				this.autograph = res.data.autograph;
				this.user_id = res.data.id;
			}
		})
		
	},
	methods: {
		back(){
			uni.navigateTo({
			    url: '../personaldata/personaldata'
			});
		},
		callback(Error){
			 console.log("name",Error);
			this.$refs.uToast.show({
				title: Error,
				type: 'default',
				// url: '../tellogin/tellogin'
			})
			 
		},
		sign(e){
			// console.log(e)
			this.autograph = e.detail.value;
		},
		save(){
			console.log(this.user_id)
			uni.request({
				url:helper.websiteUrl+"user_updateinfo",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'user_id':this.user_id,
					'autograph':this.autograph
				},
				success: (res) => {
					console.log(res)
					if(res.data.code==200){
						uni.setStorage({
							key:'userinfo',
							data:res.data.data,
							success:()=>{
								this.callback('保存成功');
							}
						});
					}else{
						this.callback(res.data.msg);
					}
					
				}
			});
		}
	}
}
